

.navbar{
    width:100%;
    height:70px;
    border:0px solid black;
background-color: aliceblue;
    color: rgb(8, 3, 33);
    display: flex;
    justify-content: space-between;
    padding:10px;
    align-items: center;
    
    position:fixed;
     margin-top: -100px; 
     /*background: linear-gradient(to right,   rgb(31, 206, 219) 0%, white 90%); */
   
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    
}


.navbar>.nav1{
    width:50%;
    height:90%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    

}

.nav1>p:hover{
    background-color: azure;
  
    color:rgb(7, 2, 23);
    border-radius:5px;
    padding:5px;

}
.img1{
    width:50px;
    height:50px;
    border-radius:50%;
    text-align: center;
}
.nav2{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:20%;
    height:90%;

}
.nav1>p>a{
    color:rgb(5, 3, 28);
    text-decoration: none;
}
.nav1>p>a:hover{
    color:rgb(1, 1, 18);
}

@media only screen and (max-width:600px ){
    div.nav1{
        display:none;
    }


  }