.load{
    width: 100%;
    height: 100%;
    background-color: rgba(9, 9, 9, 0.6);
    text-align: center;
    position: absolute;
    box-shadow: rgba(220, 220, 231, 0.755) 0px 7px 29px 0px;
    right:0%;
    margin-top: -100px;
    padding-bottom: 100px;
}

.load>img{
    text-align: center;
    margin-left: 45%;
    margin-top: 20%;
    border-radius: 50%;
}